define("appkit/routes",
  [],
  function() {
    "use strict";
    var Routes;

    Routes = function() {
      this.route("index", {
        path: "/"
      });
      this.route("starter", {
        path: "/starter/:id/"
      });
      this.route("dummy-index", {
        path: "/dummy"
      });
      return this.route("dummy", {
        path: "/dummy/:id/:bid"
      });
    };


    return Routes;
  });