define("appkit/routes/dummy-index",
  ["appkit/models/deal"],
  function(Deal) {
    "use strict";
    var WtfIndexRoute;

    WtfIndexRoute = Ember.Route.extend({
      redirect: function() {
        var bid, deal;
        deal = Deal.random();
        bid = ["1H", "1S"][Math.floor(Math.random() * 2)];
        return this.transitionTo("/dummy/" + (deal.id.toString()) + "/" + bid);
      }
    });


    return WtfIndexRoute;
  });